/* Focus styles */
input:focus-visible,
select:focus-visible,
textarea:focus-visible {
	outline-offset: 2px;
	--tw-ring-offset-shadow: var(--tw-ring-inset) 0 0 0 2px
		var(--wp--preset--color--shadcn-background);
	--tw-ring-shadow: var(--tw-ring-inset) 0 0 0 4px
		var(--wp--preset--color--shadcn-ring);
	box-shadow:
		var(--tw-ring-offset-shadow),
		var(--tw-ring-shadow),
		0 0 #0000;
}

/* Placeholder styles */
::placeholder {
	opacity: 1;
}

/* Select styles */
select {
	padding-top: 0.5rem;
	padding-right: 0.75rem;
	padding-bottom: 0.5rem;
	padding-left: 0.75rem;
	border-color: var(--wp--preset--color--shadcn-input, #e2e8f0);
	border-width: 1px;
}

/* Checkbox and Radio styles */
input[type="checkbox"],
input[type="radio"] {
	appearance: none;
	padding: 0;
	display: inline-block;
	vertical-align: middle;
	background-origin: border-box;
	user-select: none;
	flex-shrink: 0;
	height: 1rem;
	width: 1rem;
	border-color: var(--wp--preset--color--shadcn-input, #e2e8f0);
	border-width: 1px;
}

input[type="checkbox"] {
	border-radius: 0.25rem;
}

input[type="radio"] {
	border-radius: 100%;
}

input[type="checkbox"]:checked,
input[type="radio"]:checked {
	border-color: transparent;
	background-color: currentColor;
	background-size: 100% 100%;
	background-position: center;
	background-repeat: no-repeat;
}

input[type="checkbox"]:checked {
	background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 16 16' fill='white' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M12.207 4.793a1 1 0 010 1.414l-5 5a1 1 0 01-1.414 0l-2-2a1 1 0 011.414-1.414L6.5 9.086l4.293-4.293a1 1 0 011.414 0z'/%3e%3c/svg%3e");
}

input[type="radio"]:checked {
	background-image: url("data:image/svg+xml,%3csvg viewBox='0 0 16 16' fill='white' xmlns='http://www.w3.org/2000/svg'%3e%3ccircle cx='8' cy='8' r='3'/%3e%3c/svg%3e");
}

/* Disabled state */
input:disabled,
select:disabled,
textarea:disabled {
	background-color: #f7fafc;
	border-color: #edf2f7;
	color: #a0aec0;
	cursor: not-allowed;
}
