@import "./form.scss";
@import "./header-nav.scss";

:root {
	--wp--style--modal--padding: min(2rem, 4vw);
}

.relative {
	position: relative;
}
.z-1 {
	z-index: 1;
}
.z-2 {
	z-index: 2;
}
.z-3 {
	z-index: 3;
}
.z-4 {
	z-index: 4;
}
.z-5 {
	z-index: 5;
}
.z-10 {
	z-index: 10;
}

.gap-1\.5 {
	gap: var(--wp--preset--spacing--1-5);
}

.gap-2 {
	gap: var(--wp--preset--spacing--2);
}

.hidden {
	display: none;
}
.visible {
	visibility: visible;
}
.invisible {
	visibility: hidden;
}
.top-0 {
	top: 0;
}
.left-0 {
	left: 0;
}
.right-0 {
	right: 0;
}
.bottom-0 {
	bottom: 0;
}
.bottom-4 {
	bottom: 1rem;
}
.sm\:bottom-8 {
	@media (min-width: 640px) {
		bottom: 2rem;
	}
}

.top-auto {
	top: auto;
}
.absolute {
	position: absolute;
}
.relative {
	position: relative;
}
.fixed {
	position: fixed;
}
.sticky {
	position: sticky;
}

.cursor-pointer,
.open-search-modal {
	cursor: pointer;
}

.hover\:bg-accent {
	&:hover {
		background-color: var(--wp--preset--color--shadcn-accent);
		color: var(--wp--preset--color--shadcn-accent-foreground);
	}
}

.is-style-line-clamp-1,
.is-style-line-clamp-1 > a {
	overflow: hidden;
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 1;
}

.is-style-line-clamp-2,
.is-style-line-clamp-2 > a {
	overflow: hidden;
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 2;
}

.hidden-scrollbar {
	-ms-overflow-style: none;
	scrollbar-width: none;
	&::-webkit-scrollbar {
		display: none;
	}
}

//for post reaction button blocks: Like button block, Save button block, View count button block, Comment button block,
.nc-post-reaction-button {
	cursor: pointer;
	line-height: 1;
	text-decoration: none;
	font-size: var(--wp--preset--font-size--xs);
	border: 0px solid transparent;
	background-color: initial;
	background-image: none;
	&:focus {
		outline: none;
	}
	&.is-actived,
	&:hover {
		color: var(--active-color, inherit) !important;
		border-color: var(--active-border-color, inherit) !important;
		background-color: var(--active-background-color, inherit) !important;
		.wp-block-outermost-icon-block .icon-container {
			background-color: var(
				--active-icon-background-color,
				transparent
			) !important;
			color: var(--active-color, inherit) !important;
			transition-duration: 0.3s;
		}
	}
	&.is-actived {
		svg {
			fill: var(--active-color, inherit);
			color: var(--active-color, inherit);
		}
	}
	.nc__count {
		min-width: 0.5rem;
		text-align: start;
	}
}

// MODAL LIGHT BLOCK
.wp-block-cloudcatch-light-modal-block__wrapper {
	background-color: rgba(0, 0, 0, 0.4);
}
// hidden scrollbar for modal block
.wp-block-cloudcatch-light-modal-block__content {
	-ms-overflow-style: none;
	scrollbar-width: none;
	&::-webkit-scrollbar {
		display: none;
	}
}

// H2 Heading Section Block Pattern
.nc-heading-section {
	mark {
		background-color: rgba(0, 0, 0, 0);
	}
}

// LINK on GROUP BLOCK
.wp-block-group__link {
	&:focus {
		outline: none;
	}
}

// Hover for button/group have border
.button-outline {
	cursor: pointer;
	border: 1px solid var(--wp--preset--color--shadcn-border);
	transition: background-color 0.3s;
	border-radius: 12px;
	&:hover {
		background-color: var(--wp--preset--color--shadcn-accent);
	}
	&.is-active {
		background-color: var(--wp--preset--color--shadcn-accent);
	}
}
